.o-icon {
  display: inline-block;
  flex-shrink: 0;
  width: var(--step-0);
  height: var(--step-0);
  vertical-align: middle;
  font-size: 0;
  fill: none;
  stroke: currentColor;
  stroke-width: $icon-stroke-width;
}

.o-icon__svg {
  width: 100%;
  height: 100%;
}

.o-icon--arrow-90 {
  polyline {
    fill: currentColor;
  }

  polygon {
    fill: currentColor;
  }
}
